import Vue from 'vue'
import Router from 'vue-router'
//import HelloWorld from '@/components/HelloWorld'
//import YoYoYo from '@/components/YoYoYo'
const YoYoYo = () => import(/* webpackChunkName: "YoYoYo" */ '@/components/YoYoYo.vue');
const HelloWorld = () => import(/* webpackChunkName: "HelloWorld" */ '@/components/HelloWorld.vue');
const NoNoNo = () => import(/* webpackChunkName: "NoNoNo" */ '@/components/NoNoNo.vue');

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/YoYoYo',
      name: 'YoYoYo',
      component: YoYoYo
    },
    {
      path: '/NoNoNo',
      name: 'NoNoNo',
      component: NoNoNo
    }
  ]
})



